import { URL } from '../requester';
import { UrlQuery } from '../requester/constants';
import { REAL_TIME_EVENT } from './constants';
import { IExternalEventSource, IExternalEventSourceConstructor } from './types';

export interface IEventSource {
  open: (onEvent: (timestamp: number) => void) => void;
  close: () => void;
}

export class ExternalEventSource implements IEventSource {
  private eventSource: IExternalEventSource;
  private siteCode: string;

  constructor(
    siteCode: string,
    externalEventSource: IExternalEventSourceConstructor,
  ) {
    const url = URL.EVENTS + UrlQuery.Sse + siteCode;
    const eventSource = externalEventSource.initialize(url);

    this.eventSource = eventSource;
    this.siteCode = siteCode;
  }

  public open(onEvent: (timestamp: number) => void): void {
    this.eventSource.open({
      onEvent,
      siteCode: this.siteCode,
      eventType: REAL_TIME_EVENT,
    });
  }

  public close(): void {
    this.eventSource.close();
  }
}
