import { Err, Ok, Result } from 'ts-res';
import { CacheManager } from './cacheManager';
import { CampaignConfiguration, JSONType } from './campaignConfiguration';
import { ClientSettings, ClientSettingsType } from './clientSettings';
import {
  CACHE_CLEANUP_TIMEOUT,
  OFF_VARIATION_KEY,
  UNALLOCATED_VARIATION_ID,
} from './constants';
import { Conversion, KameleoonDataType } from './kameleoonData';
import { KameleoonError, KameleoonException } from './kameleoonError';
import { Requester, TrackExperimentParamsType } from './requester';
import {
  ClientDataType,
  KameleoonStorageKey,
  VariationDataType,
} from './storage';
import { ExternalStorage } from './storage/externalStorage';
import {
  TrackConversionParamsType,
  ExperimentType,
  FeatureFlagType,
  GetFeatureFlagVariableParamsType,
  FeatureVariableResultType,
  InternalSDKConfigurationType,
  SDKCoreParameters,
  TrackingCacheItemType,
} from './types';
import { Utilities } from './utilities';
import {
  FeatureFlagVariationType,
  VariationConfiguration,
} from './variationConfiguration';

/**
 * @interface an interface of KameleoonClient instance
 */
export interface IKameleoonClient {
  /**
   * @method initialize - an asynchronous method for KameleoonClient initialization by fetching Kameleoon SDK related data from server or by retrieving data from local source if data is up-to-date or update interval has not been reached
   * @returns {Promise<boolean>} Promise resolved into boolean field indicating success or fail
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.StorageWrite` Couldn't update storage data
   * - `KameleoonException.ClientConfiguration` Couldn't retrieve client configuration from Kameleoon Api
   */
  initialize: () => Promise<boolean>;
  /**
   * @method addData - method for adding targeting data to the storage so that other methods could decide whether the current visitor is targeted or not. Note: userAgent data will not be stored in storage like other data, and it will be sent with every tracking request for bot filtration.
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @param {KameleoonDataType[]} kameleoonData - number of instances of any type of `KameleoonData`, can be added solely in array or as a sequential arguments
   * @returns {void}
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` The visitor code length was exceeded
   * - `KameleoonException.StorageWrite` Couldn't update storage data
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  addData: (visitorCode: string, ...data: KameleoonDataType[]) => void;
  /**
   * @method triggerExperiment - triggers experiment by assigning the variation to the user with `visitorCode`, if the variation is already assigned just returns it's id. Note: returned id `0` indicates default variation.  At the same time executes `flushData` without sending extra request.
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @param {number} experimentId - id of experiment running for the current visitor
   * @returns {number} associated variationId which is successfully searched/assigned
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.ExperimentConfigurationNotFound` No configuration found for provided `experimentId`
   * - `KameleoonException.VisitorCodeMaxLength` The visitor code length was exceeded
   * - `KameleoonException.StorageRead` Couldn't find associated experiment by provided `experimentId` and `visitorCode` inside the storage
   * - `KameleoonException.NotTargeted` Current visitor is not targeted
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  triggerExperiment: (visitorCode: string, experimentId: number) => number;
  /**
   * @method trackConversion - creates and adds `Conversion` data to the visitor with specified parameters and executes `flushData`. Note: it's a helper method for the quick and convenient conversion tracking, however creating and adding `Conversion` manually allows more flexible `Conversion` with `negative` parameter
   * @param {TrackConversionParamsType} conversionParameters - `visitorCode`, `goalId` and `revenue` parameters for data tracking
   * @returns {void}
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` The visitor code length was exceeded
   * - `KameleoonException.StorageWrite` Couldn't update storage data
   */
  trackConversion: ({
    visitorCode,
    goalId,
    revenue,
  }: TrackConversionParamsType) => void;
  /**
   * @method flushData - takes visitor associated kameleoon data and sends the data tracking request with collected data
   * sent data is removed from `KameleoonClient` instance but not from the storage for correct targeting checks
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @returns {void}
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  flushData: (visitorCode: string) => void;
  /**
   * @method getExperiments - returns a list of experiments stored in the client configuration
   * @returns {ExperimentType[]} a list of all experiments items with `id` and `name` fields
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  getExperiments: () => ExperimentType[];
  /**
   * @method getVisitorExperiments - returns a list of experiments that the visitor with `visitorCode` is targeted by and that are *active* for the visitor (visitor will have one of the variations allocated if the experiment will be triggered).
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @param {boolean | undefined} isAllocated - boolean value indicating that only experiments *allocated* for visitor will be returned. Default value is `true`
   * @returns {ExperimentType[]} a list of all experiments items with `id` and `name` fields
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` - The visitor code length was exceeded
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  getVisitorExperiments: (
    visitorCode: string,
    isAllocated?: boolean,
  ) => ExperimentType[];
  /**
   * @method getExperimentVariationData - returns variation data in JSON format for the variation with `variationId`
   * @param {number} variationId - id of variation
   * @returns {JSONType} variation data in JSON format
   * @throws `KameleoonError` with one of the following `type` s:
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   * - `KameleoonException.JSONParse` - Couldn't pass JSON value
   * - `KameleoonException.VariationNotFound` - No variation found for provided `variationId`
   */
  getExperimentVariationData: (variationId: number) => JSONType | null;
  /**
   * @method getFeatureFlags - returns a list of feature flags stored in the client configuration
   * @returns {FeatureFlagType[]} a list of all feature flags items with `id` and `key` fields
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  getFeatureFlags: () => FeatureFlagType[];
  /**
   * @method getVisitorFeatureFlags - returns a list of feature flags that the visitor with `visitorCode` that is targeted by and that are *active* for the visitor (visitor will have one of the variations allocated).
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @returns {FeatureFlagType[]} a list of all feature flags items with `id` and `key` fields
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` - The visitor code length was exceeded
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   * - `KameleoonException.NotTargeted` - Current visitor is not targeted
   */
  getVisitorFeatureFlags: (visitorCode: string) => FeatureFlagType[];
  /**
   * @method isFeatureFlagActive - returns a boolean indicating whether the visitor with `visitorCode` has `featureKey` active for him, this method includes targeting check, finding the according variation exposed to the visitor and saving it to storage along with sending tracking request
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @param {string} featureKey - a unique key for feature flag
   * @returns {boolean} a boolean indicator of whether the feature flag with `featureKey` is active for visitor with `visitorCode`
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` - The visitor code length was exceeded
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   * - `KameleoonException.FeatureFlagConfigurationNotFound` - No feature flag was found for provided `visitorCode` and `featureKey`
   * - `KameleoonException.DataInconsistency` - Allocated variation was found but there is no feature flag with according `featureKey`.
   * - `KameleoonException.NotTargeted` - Current visitor is not targeted
   */
  isFeatureFlagActive: (visitorCode: string, featureKey: string) => boolean;
  /**
   * @method getFeatureFlagVariationKey - returns variation key for the visitor under `visitorCode` in the found feature flag, this method includes targeting check, finding the according variation exposed to the visitor and saving it to storage along with sending tracking request
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @param {string} featureKey - a unique key for feature flag
   * @returns {string} a string containing variable key for the allocated feature flag variation for the provided visitor
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` - The visitor code length was exceeded
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   * - `KameleoonException.FeatureFlagConfigurationNotFound` - No feature flag was found for provided `visitorCode` and `featureKey`
   * - `KameleoonException.NotTargeted` - Current visitor is not targeted
   */
  getFeatureFlagVariationKey: (
    visitorCode: string,
    featureKey: string,
  ) => string;
  /**
   * @method getFeatureFlagVariable - returns a variable for the visitor under `visitorCode` in the found feature flag, this method includes targeting check, finding the according variation exposed to the visitor and saving it to storage along with sending tracking request
   * @param {GetFeatureFlagVariableParamsType} parameters - an object with parameters of a type `GetFeatureFlagVariableParamsType`, see the type for details.
   * @returns {FeatureVariableResultType} a variable object containing `type` and `value` fields, `type` can be checked against `FeatureVariableType` enum, if the `type` is `FeatureVariableType.BOOLEAN` then the `value` type will be `boolean` and so on.
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.VisitorCodeMaxLength` - The visitor code length was exceeded
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   * - `KameleoonException.FeatureFlagConfigurationNotFound` - No feature flag was found for provided `visitorCode` and `featureKey`
   * - `KameleoonException.NotTargeted` - Current visitor is not targeted
   * - `KameleoonException.JSONParse` - Couldn't pass JSON value
   * - `KameleoonException.NumberParse` - Couldn't pass Number value
   */
  getFeatureFlagVariable: ({
    visitorCode,
    featureKey,
    variableKey,
  }: GetFeatureFlagVariableParamsType) => FeatureVariableResultType;
  /**
   * @method getRemoteData - returns a data which is stored for specified siteCode on a remote Kameleoon server.
   * @param {string} key - unique key that the data you try to get is associated with.
   * @returns {JSONType} - promise with retrieving data for specific key.
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.RemoteData` - Couldn't retrieve data from Kameleoon server
   */
  getRemoteData: (key: string) => Promise<JSONType>;
  /**
   * @method getEngineTrackingCode - returns Kameleoon tracking code for the current visitor. Tracking code is built of the experiments that were triggered and feature flag variations that were assigned during the last 5 seconds
   * See [Kameleoon Automation API](https://developers.kameleoon.com/apis/activation-api-js/api-reference#trigger-1) for the details
   * @param {string} visitorCode - unique visitor identification string, can't exceed 255 characters length
   * @returns {string} Kameleoon tracking code
   */
  getEngineTrackingCode: (visitorCode: string) => string;
  /**
   * @method onConfigurationUpdate - fires a callback on client configuration update. Note: this method only works for server sent events of real time update.
   * @param {() => void} callback - callback function with no parameters that will be called upon configuration update
   * @returns {void}
   * @throws `KameleoonError` with one of the following `type` s:
   *
   * - `KameleoonException.Initialization` - Method was executed before `initialize` was done for `kameleoonClient`
   */
  onConfigurationUpdate: (callback: () => void) => void;
}

/**
 * @class
 * KameleoonClient - a class for creating kameleoon client instance
 * for communicating with Kameleoon JavaScript SDK Core, when new instance is created
 */
export class KameleoonClient implements IKameleoonClient {
  private internalConfiguration: InternalSDKConfigurationType;
  private variationConfiguration?: VariationConfiguration;
  private campaignConfiguration?: CampaignConfiguration;
  private settings: ClientSettingsType;
  private trackingCache: CacheManager<TrackingCacheItemType>;
  private requester: Requester;

  /**
   * @param {SDKCoreParameters} sdkCoreParameters - parameters for initializing sdk core
   */
  constructor({
    siteCode,
    configuration,
    internalConfiguration,
  }: SDKCoreParameters) {
    const { settings: clientSettings } = new ClientSettings(
      siteCode,
      configuration,
    );
    const { externalPackageInfo, externalRequestDispatcher } =
      internalConfiguration;
    const requester = new Requester({
      siteCode,
      environment: clientSettings.environment,
      packageInfo: externalPackageInfo,
      requestDispatcher: externalRequestDispatcher,
    });
    const trackingCache = new CacheManager<TrackingCacheItemType>(
      CACHE_CLEANUP_TIMEOUT,
    );

    this.requester = requester;
    this.settings = clientSettings;
    this.trackingCache = trackingCache;
    this.internalConfiguration = internalConfiguration;
  }

  public async initialize(): Promise<boolean> {
    const {
      externalStorage,
      externalEventSource,
      externalClientConfiguration,
      targetingDataCleanupInterval,
    } = this.internalConfiguration;

    const clientDataStorage = new ExternalStorage<ClientDataType>(
      externalStorage.initialize(KameleoonStorageKey.ClientData),
    );
    const variationDataStorage = new ExternalStorage<VariationDataType>(
      externalStorage.initialize(KameleoonStorageKey.VariationData),
    );

    const campaignConfiguration = new CampaignConfiguration({
      settings: this.settings,
      storage: clientDataStorage,
      requester: this.requester,
      externalClientConfiguration,
      targetingCleanupInterval: targetingDataCleanupInterval,
    });

    const result = await campaignConfiguration.initialize(externalEventSource);
    result.throw();

    const variationConfiguration = new VariationConfiguration(
      campaignConfiguration.experiments,
      variationDataStorage,
    );

    this.variationConfiguration = variationConfiguration;
    this.campaignConfiguration = campaignConfiguration;

    return result.ok;
  }

  public addData(visitorCode: string, ...data: KameleoonDataType[]): void {
    Utilities.validateVisitorCode(visitorCode).throw();

    if (!this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    this.campaignConfiguration.addTargetingData(visitorCode, ...data).throw();
  }

  public triggerExperiment(visitorCode: string, experimentId: number): number {
    Utilities.validateVisitorCode(visitorCode).throw();

    if (!this.campaignConfiguration || !this.variationConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    const experiment = this.campaignConfiguration.experiments.find(
      (item) => item.id === String(experimentId),
    );

    if (!experiment) {
      throw new KameleoonError(
        KameleoonException.ExperimentConfigurationNotFound,
        experimentId,
        visitorCode,
      );
    }

    const userAgent = Utilities.getUserAgent(
      visitorCode,
      this.campaignConfiguration,
    );
    const dataUrl = Utilities.getDataUrl(
      visitorCode,
      this.campaignConfiguration,
    );
    const targetingData =
      this.campaignConfiguration.storedTargetingData[visitorCode];
    const trackingParameters: Omit<
      TrackExperimentParamsType,
      'variationId' | 'isUnallocated'
    > = {
      body: dataUrl,
      visitorCode,
      experimentId,
    };

    let variationId = UNALLOCATED_VARIATION_ID;
    let isUnallocated = true;

    const targetingResult = Utilities.checkTargeting({
      visitorCode,
      experimentId,
      targetingData,
      segment: experiment.segment,
      variationConfiguration: this.variationConfiguration,
    }).throw();

    variationId = this.variationConfiguration
      .getAssociatedVariationId(visitorCode, experimentId)
      .else((error) => {
        if (error.type === KameleoonException.NotAllocated) {
          this.requester.trackExperiment({
            isUnallocated: true,
            variationId,
            userAgent,
            ...trackingParameters,
          });

          return UNALLOCATED_VARIATION_ID;
        }

        throw error;
      });

    this.requester.trackExperiment({
      variationId,
      isUnallocated,
      userAgent,
      ...trackingParameters,
    });

    if (!targetingResult) {
      throw new KameleoonError(KameleoonException.NotTargeted, visitorCode);
    }

    Utilities.updateCache({
      cacheManager: this.trackingCache,
      visitorCode,
      experimentId,
      variationId,
    });

    return variationId;
  }

  public trackConversion({
    visitorCode,
    goalId,
    revenue,
  }: TrackConversionParamsType): void {
    this.addData(visitorCode, new Conversion({ goalId, revenue }));
    this.flushData(visitorCode);
  }

  public flushData(visitorCode: string): void {
    if (!this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    const dataUrl = Utilities.getDataUrl(
      visitorCode,
      this.campaignConfiguration,
    );

    const userAgent = Utilities.getUserAgent(
      visitorCode,
      this.campaignConfiguration,
    );

    this.requester.trackData({ visitorCode, body: dataUrl, userAgent });
  }

  public getExperiments(): ExperimentType[] {
    if (!this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    return this.campaignConfiguration.experiments.map(({ id, name }) => ({
      id: Number(id),
      name,
    }));
  }

  public getVisitorExperiments(
    visitorCode: string,
    isActive = true,
  ): ExperimentType[] {
    if (!this.variationConfiguration || !this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    Utilities.validateVisitorCode(visitorCode).throw();

    const experiments = this.campaignConfiguration.experiments;
    const result: ExperimentType[] = [];

    const targetingData =
      this.campaignConfiguration.storedTargetingData[visitorCode];

    for (const experiment of experiments) {
      const { id, name, segment } = experiment;

      const targetingResult = Utilities.checkTargeting({
        targetingData,
        visitorCode,
        segment,
        variationConfiguration: this.variationConfiguration,
        experimentId: Number(id),
      }).throw();

      if (!targetingResult) {
        continue;
      }

      // -- If `isActive=false` then return every experiment
      if (!isActive) {
        result.push({ id: Number(id), name });
        continue;
      }

      // -- Else first check if there are saved variations for the experiment
      // if there is at least one than it's active
      const experimentVariations =
        this.variationConfiguration.getAssignedVariations(visitorCode);

      if (experimentVariations.ok) {
        const experimentVariation = experimentVariations.data[experiment.id];

        if (experimentVariation) {
          result.push({ id: Number(id), name });
          continue;
        }
      }

      // -- Second check if the experiment will have an allocated variation upon triggering
      // if yes than it's active
      const variationResult =
        this.variationConfiguration.getExperimentVariation(
          visitorCode,
          experiment,
        );

      if (variationResult.ok) {
        result.push({ id: Number(id), name });
      }
    }

    return result;
  }

  public getExperimentVariationData(variationId: number): JSONType {
    if (!this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    const experiments = this.campaignConfiguration.experiments;

    for (const experiment of experiments) {
      const variation = experiment.variations.find(
        (item) => item.id === String(variationId),
      );

      if (variation) {
        try {
          return JSON.parse(variation.customJson);
        } catch (err) {
          throw new KameleoonError(KameleoonException.JSONParse, err);
        }
      }
    }

    throw new KameleoonError(KameleoonException.VariationNotFound);
  }

  public getFeatureFlags(): FeatureFlagType[] {
    if (!this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    return this.campaignConfiguration.featureFlags.map(
      ({ id, featureKey }) => ({ id, key: featureKey }),
    );
  }

  public getVisitorFeatureFlags(visitorCode: string): FeatureFlagType[] {
    if (!this.variationConfiguration || !this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    const featureFlags = this.campaignConfiguration.featureFlags;
    const resultFeatureFlags: FeatureFlagType[] = [];

    const targetingData =
      this.campaignConfiguration.storedTargetingData[visitorCode];

    for (const featureFlag of featureFlags) {
      const variationData = this.variationConfiguration
        .getFeatureFlagVariation({
          visitorCode,
          targetingData,
          featureFlag,
        })
        .throw();

      if (variationData.variationKey !== OFF_VARIATION_KEY) {
        const { featureFlagId, featureKey } = variationData;

        resultFeatureFlags.push({ id: featureFlagId, key: featureKey });
      }
    }

    return resultFeatureFlags;
  }

  public getFeatureFlagVariationKey(
    visitorCode: string,
    featureKey: string,
  ): string {
    const { variationKey } = this._getFeatureFlagVariation(
      visitorCode,
      featureKey,
    ).throw();

    return variationKey;
  }

  public getFeatureFlagVariable({
    visitorCode,
    featureKey,
    variableKey,
  }: GetFeatureFlagVariableParamsType): FeatureVariableResultType {
    const { variationKey } = this._getFeatureFlagVariation(
      visitorCode,
      featureKey,
    ).throw();

    // --- Note ---
    // `_getFeatureFlagVariation` already checks `campaignConfiguration` initialization
    const featureFlags = (this.campaignConfiguration as CampaignConfiguration)
      .featureFlags;

    const variable = featureFlags
      .find((item) => item.featureKey === featureKey)
      ?.variations.find((item) => item.key === variationKey)
      ?.variables.find((item) => item.key === variableKey);

    if (!variable) {
      throw new KameleoonError(
        KameleoonException.FeatureFlagConfigurationNotFound,
        featureKey,
        visitorCode,
      );
    }

    const featureVariable = Utilities.parseFeatureVariable(variable).throw();

    return featureVariable;
  }

  public isFeatureFlagActive(visitorCode: string, featureKey: string): boolean {
    const variationKey = this.getFeatureFlagVariationKey(
      visitorCode,
      featureKey,
    );

    return variationKey !== OFF_VARIATION_KEY;
  }

  public async getRemoteData(key: string): Promise<JSONType> {
    const remoteData = await this.requester.getRemoteData(key);

    return remoteData.throw();
  }

  public onConfigurationUpdate(callback: () => void): void {
    if (!this.campaignConfiguration) {
      throw new KameleoonError(KameleoonException.Initialization);
    }

    this.campaignConfiguration.onConfigurationUpdate(callback);
  }

  public getEngineTrackingCode(visitorCode: string): string {
    return Utilities.getTrackingCode(this.trackingCache, visitorCode);
  }

  private _getFeatureFlagVariation(
    visitorCode: string,
    featureKey: string,
  ): Result<FeatureFlagVariationType, KameleoonError> {
    if (!this.variationConfiguration || !this.campaignConfiguration) {
      return Err(new KameleoonError(KameleoonException.Initialization));
    }

    const featureFlags = this.campaignConfiguration.featureFlags;
    const featureFlag = featureFlags.find(
      (item) => item.featureKey === featureKey,
    );

    if (!featureFlag) {
      return Err(
        new KameleoonError(
          KameleoonException.FeatureFlagConfigurationNotFound,
          featureKey,
          visitorCode,
        ),
      );
    }

    const targetingData =
      this.campaignConfiguration.storedTargetingData[visitorCode];

    const variationData = this.variationConfiguration
      .getFeatureFlagVariation({
        visitorCode,
        featureFlag,
        targetingData,
        withAssignment: true,
      })
      .throw();

    const { experimentId, variationId, rule } = variationData;

    if (typeof variationId === 'number' && typeof experimentId === 'number') {
      Utilities.updateCache({
        cacheManager: this.trackingCache,
        visitorCode,
        experimentId,
        variationId,
      });
    }

    const userAgent = Utilities.getUserAgent(
      visitorCode,
      this.campaignConfiguration,
    );

    if (rule) {
      Utilities.trackRule({
        visitorCode,
        requester: this.requester,
        featureFlagVariation: variationData,
        campaignConfiguration: this.campaignConfiguration,
        userAgent,
      });
    }

    return Ok(variationData);
  }
}
