import { KameleoonStorageKey } from '../storage';
import { TargetingType } from '../targeting';
import { ERROR_MESSAGES } from './constants';
import { exhaustCheck } from './helpers';
import { KameleoonException } from './types';

/**
 * @class
 * KameleoonError - extends standard `Error` by adding custom `type` property with the type of `KameleoonException`
 * */
export class KameleoonError extends Error {
  private errorType: KameleoonException;

  /**
   * @param {KameleoonException} type - type of KameleoonError represented by `KameleoonException` enum
   * */
  constructor(
    type:
      | KameleoonException.Initialization
      | KameleoonException.ClientConfiguration
      | KameleoonException.VisitorCodeMaxLength
      | KameleoonException.StorageInitialization
      | KameleoonException.EventSourceInitialization
      | KameleoonException.VariationNotFound
      | KameleoonException.RemoteData,
  );
  constructor(
    type: KameleoonException.FeatureFlagConfigurationNotFound,
    featureKey: string,
    visitorCode: string,
  );
  constructor(
    type: KameleoonException.NotTargeted | KameleoonException.NotAllocated,
    visitorCode: string,
  );
  constructor(
    type: KameleoonException.ExperimentConfigurationNotFound,
    experimentId: number,
    visitorCode: string,
  );
  constructor(
    type: KameleoonException.StorageWrite | KameleoonException.JSONParse,
    err: unknown,
  );
  constructor(type: KameleoonException.StorageRead, storageKey: string);
  constructor(
    type: KameleoonException.StorageParse,
    err: unknown,
    key: KameleoonStorageKey,
  );
  constructor(
    type: KameleoonException.TargetingCondition,
    targetingType: TargetingType,
  );
  constructor(
    type: KameleoonException.AmongValuesCheck,
    err: unknown,
    value: string,
  );
  constructor(type: KameleoonException.NumberParse, value: string);
  constructor(
    type: KameleoonException,
    secondParam?:
      | number
      | string
      | unknown
      | KameleoonStorageKey
      | TargetingType,
    thirdParam?: string | KameleoonStorageKey,
  ) {
    super(`Error: ${type}`);

    this.name = 'KameleoonError';
    this.errorType = type;

    switch (type) {
      case KameleoonException.Initialization:
      case KameleoonException.ClientConfiguration:
      case KameleoonException.VisitorCodeMaxLength:
      case KameleoonException.StorageInitialization:
      case KameleoonException.VariationNotFound:
      case KameleoonException.EventSourceInitialization:
      case KameleoonException.RemoteData:
        this.message = ERROR_MESSAGES[type]();
        break;
      case KameleoonException.NotAllocated:
      case KameleoonException.NotTargeted:
        this.message = ERROR_MESSAGES[type](secondParam as string);
        break;
      case KameleoonException.ExperimentConfigurationNotFound:
        this.message = ERROR_MESSAGES[type](
          secondParam as number,
          thirdParam as string,
        );
        break;
      case KameleoonException.FeatureFlagConfigurationNotFound:
        this.message = ERROR_MESSAGES[type](
          secondParam as string,
          thirdParam as string,
        );
        break;
      case KameleoonException.StorageWrite:
      case KameleoonException.JSONParse:
        this.message = ERROR_MESSAGES[type](secondParam);
        break;
      case KameleoonException.StorageRead:
        this.message = ERROR_MESSAGES[type](secondParam as KameleoonStorageKey);
        break;
      case KameleoonException.StorageParse:
        this.message = ERROR_MESSAGES[type](
          secondParam,
          thirdParam as KameleoonStorageKey,
        );
        break;
      case KameleoonException.TargetingCondition:
        this.message = ERROR_MESSAGES[type](secondParam as TargetingType);
        break;
      case KameleoonException.NumberParse:
        this.message = ERROR_MESSAGES[type](secondParam as string);
        break;
      case KameleoonException.AmongValuesCheck:
        this.message = ERROR_MESSAGES[type](secondParam, thirdParam as string);
        break;
      default:
        exhaustCheck(type);
    }
  }

  get type(): KameleoonException {
    return this.errorType;
  }
}
