import { Err, Ok, Result } from 'ts-res';
import {
  KameleoonError,
  KameleoonException,
  exhaustCheck,
} from '../kameleoonError';
import { ExperimentVariationsType } from '../storage';
import {
  CheckTargetingParamsType,
  ManageCacheParametersType,
  TrackRuleParamsType,
} from './types';
import {
  FeatureVariableResultType,
  Milliseconds,
  TrackingCacheItemType,
  VariableType,
} from '../types';
import { TargetingTree } from '../targeting';
import { VISITOR_CODE_MAX_LENGTH } from './constants';
import {
  CampaignConfiguration,
  FeatureVariableType,
  JSONType,
} from '../campaignConfiguration';
import {
  KameleoonData,
  KameleoonDataItemType,
  UserAgentDataType,
} from '../kameleoonData';
import { CACHE_ITEM_LIFETIME } from '../constants';
import { CacheManager } from '../cacheManager';

export class Utilities {
  static checkTargeting({
    segment,
    visitorCode,
    targetingData,
    experimentId,
    variationConfiguration,
  }: CheckTargetingParamsType): Result<boolean, KameleoonError> {
    if (!segment || !segment.conditionsData.firstLevel.length) {
      return Ok(true);
    }

    if (!variationConfiguration) {
      return Err(new KameleoonError(KameleoonException.Initialization));
    }

    let variations: ExperimentVariationsType = {};

    const assignedVariationsResult =
      variationConfiguration.getAssignedVariations(visitorCode);

    if (assignedVariationsResult.ok) {
      variations = assignedVariationsResult.data;
    }

    const targetingTree = new TargetingTree(segment);

    const result = targetingTree.evaluate({
      targetingData: targetingData || [],
      variationData: variations,
      experimentId,
    });

    return result;
  }

  static validateVisitorCode(
    visitorCode: string,
  ): Result<void, KameleoonError> {
    if (visitorCode.length > VISITOR_CODE_MAX_LENGTH) {
      return Err(new KameleoonError(KameleoonException.VisitorCodeMaxLength));
    }

    return Ok();
  }

  static getDataUrl(
    visitorCode: string,
    campaignConfiguration: CampaignConfiguration,
  ): string {
    const unsentData = campaignConfiguration.getUnsentData(visitorCode);

    return unsentData.map((item) => item.url).join('\n');
  }

  static trackRule({
    featureFlagVariation,
    campaignConfiguration,
    visitorCode,
    requester,
    userAgent,
  }: TrackRuleParamsType): void {
    const { variationId, rule } = featureFlagVariation;

    if (!rule) {
      throw new Error(
        'Internal usage error, make sure to use `trackRule` for existing rule',
      );
    }

    const dataUrl = this.getDataUrl(visitorCode, campaignConfiguration);

    const resultVariationId = variationId || 0;
    const isUnallocated = variationId === null;
    const callback = () => campaignConfiguration.clearUnsentData;

    requester.trackExperiment({
      visitorCode,
      isUnallocated,
      body: dataUrl,
      callback,
      experimentId: rule.experimentId,
      variationId: resultVariationId,
      userAgent,
    });
  }

  static parseFeatureVariable(
    variable: FeatureVariableType,
  ): Result<FeatureVariableResultType, KameleoonError> {
    const { type, value } = variable;

    switch (type) {
      case VariableType.BOOLEAN:
        return Ok({
          type,
          value: Boolean(value),
        });
      case VariableType.STRING:
        return Ok({
          type,
          value: String(value),
        });
      case VariableType.NUMBER:
        const numberValue = Number(value);

        if (Number.isNaN(numberValue)) {
          return Err(
            new KameleoonError(KameleoonException.NumberParse, String(value)),
          );
        }

        return Ok({
          type,
          value: numberValue,
        });
      case VariableType.JSON:
        try {
          const jsonValue: JSONType = JSON.parse(String(variable.value));

          return Ok({
            type,
            value: jsonValue,
          });
        } catch (err) {
          return Err(new KameleoonError(KameleoonException.JSONParse, err));
        }
      default:
        exhaustCheck(type);
    }
  }

  static getUserAgent(
    visitorCode: string,
    campaignConfiguration: CampaignConfiguration,
  ): string | undefined {
    const isUserAgent = (
      data: KameleoonDataItemType,
    ): data is UserAgentDataType => data.type === KameleoonData.UserAgent;
    const targetingData =
      campaignConfiguration.storedTargetingData[visitorCode];

    if (targetingData) {
      const filteredData = targetingData
        .map((item) => item.data)
        .filter(isUserAgent);

      if (filteredData.length) {
        const [userAgentData] = filteredData;

        return userAgentData.value;
      }
    }

    return undefined;
  }

  static updateCache({
    cacheManager,
    visitorCode,
    experimentId,
    variationId,
  }: ManageCacheParametersType): void {
    const cacheData = cacheManager.getAliveItem(visitorCode);

    const newCacheData = cacheData ? { ...cacheData } : {};

    newCacheData[experimentId] = {
      variationId,
      expirationTime: Date.now() + CACHE_ITEM_LIFETIME * Milliseconds.Second,
    };

    cacheManager.add({
      key: visitorCode,
      data: newCacheData,
      lifetime: CACHE_ITEM_LIFETIME,
    });
  }

  static getTrackingCode(
    trackingCache: CacheManager<TrackingCacheItemType>,
    visitorCode: string,
  ): string {
    let resultString = 'window.kameleoonQueue=window.kameleoonQueue||[];';

    const cacheData = trackingCache.getAliveItem(visitorCode);

    if (!cacheData) {
      return resultString;
    }

    const updatedCacheData: TrackingCacheItemType = {};

    Object.entries(cacheData).forEach(
      ([experimentId, { variationId, expirationTime }]) => {
        if (expirationTime > Date.now()) {
          resultString += `window.kameleoonQueue.push(['Experiments.assignVariation',${experimentId},${variationId}]);`;
          resultString += `window.kameleoonQueue.push(['Experiments.trigger',${experimentId},true]);`;

          updatedCacheData[Number(experimentId)] = {
            variationId,
            expirationTime,
          };
        }
      },
    );

    // --- Note ---
    // Cache is updated with only alive experiments
    trackingCache.add({
      key: visitorCode,
      data: updatedCacheData,
      lifetime: CACHE_ITEM_LIFETIME,
    });

    return resultString;
  }
}
