import { UrlEventType, UrlParameter } from '../requester';
import { Nonce } from './nonce';
import {
  ConversionDataType,
  ConversionParametersType,
  IKameleoonData,
  KameleoonData,
} from './types';

/**
 * @class
 * Conversion - a class for creating an instance for conversion tracking
 * */
export class Conversion extends Nonce implements IKameleoonData {
  private goalId: number;
  private revenue: number;
  private negative: boolean;

  /**
   * @param {ConversionParametersType} converstionParameters - an object with conversion parameters
   * */
  constructor({
    goalId,
    revenue = 0,
    negative = false,
  }: ConversionParametersType) {
    super();

    this.goalId = goalId;
    this.revenue = revenue;
    this.negative = negative;
  }

  /**
   * @property url - url for the current data type used for events tracking
   * */
  get url(): string {
    return (
      UrlEventType.Conversion +
      UrlParameter.GoalId +
      String(this.goalId) +
      UrlParameter.Revenue +
      String(this.revenue) +
      UrlParameter.Negative +
      String(this.negative) +
      UrlParameter.Nonce +
      this.nonce
    );
  }

  /**
   * @property data - data of the current class instance
   * */
  get data(): ConversionDataType {
    return {
      goalId: this.goalId,
      revenue: this.revenue,
      negative: this.negative,
      type: KameleoonData.Conversion,
      nonce: this.nonce,
    };
  }
}
