import { Ok, Result } from 'ts-res';
import { IExternalStorage, IStorage, StorageDataType } from './types';
import { KameleoonError, KameleoonException } from '../kameleoonError';
import { KameleoonStorageKey } from './constants';

export class ExternalStorage<T extends StorageDataType> implements IStorage<T> {
  private key: KameleoonStorageKey;
  private storage: IExternalStorage;

  constructor(externalStorage: IExternalStorage) {
    this.key = externalStorage.key;
    this.storage = externalStorage;
  }

  public read(): Result<T, KameleoonError> {
    const result = this.storage.read();

    if (!result.ok) {
      return result;
    }

    return this.parse(result.data);
  }

  public write(data: T): Result<void, KameleoonError> {
    this.storage.write(JSON.stringify(data));

    return Ok();
  }

  private parse(data: string): Result<T, KameleoonError> {
    let result: T;

    try {
      result = JSON.parse(data);
    } catch (err) {
      throw new KameleoonError(KameleoonException.StorageParse, err, this.key);
    }

    return Ok(result);
  }
}
