import { KameleoonError } from '../kameleoonError';
import { ConditionFactory, ConditionDataType } from './conditions';
import { NodeValueType, Operator } from './types';

export interface ITargetingNode {
  readonly value?: NodeValueType;
  readonly left?: ITargetingNode;
  readonly right?: ITargetingNode;
  readonly error?: KameleoonError;
}

export class TargetingNode implements ITargetingNode {
  private nodeValue?: NodeValueType;
  private nodeError?: KameleoonError;
  private leftChild?: ITargetingNode;
  private rightChild?: ITargetingNode;

  constructor(
    value: ConditionDataType | Operator,
    left?: ITargetingNode,
    right?: ITargetingNode,
  ) {
    if (this.isOperator(value)) {
      this.nodeValue = value;
    } else {
      const result = ConditionFactory.createCondition(value);

      if (result.ok) {
        this.nodeValue = result.data;
      } else {
        this.nodeError = result.error;
      }
    }

    this.leftChild = left;
    this.rightChild = right;
  }

  get value(): NodeValueType | undefined {
    return this.nodeValue;
  }

  get error(): KameleoonError | undefined {
    return this.nodeError;
  }

  get left(): ITargetingNode | undefined {
    return this.leftChild;
  }

  get right(): ITargetingNode | undefined {
    return this.rightChild;
  }

  private isOperator(value: ConditionDataType | Operator): value is Operator {
    return value === Operator.And || value === Operator.Or;
  }
}
