import {
  RealTimeEventType,
  IExternalEventSource,
  EventSourceOpenParametersType,
  IExternalEventSourceConstructor,
} from '@kameleoon/javascript-sdk-core';

export class EventSourceConstructor implements IExternalEventSourceConstructor {
  public initialize(url: string): IExternalEventSource {
    return new ExternalEventSource(url);
  }
}

// --- Note ---
// - `EventSource` has no dedicated tests because same browser instance
// is tested thoroughly inside core and would just duplicate tests
// - Moreover we would have to import some unexposed details like constructed url
// or the `eventType` from the core
class ExternalEventSource implements IExternalEventSource {
  private eventSource: EventSource;

  constructor(url: string) {
    const eventSource = new EventSource(url);

    this.eventSource = eventSource;
  }

  public open({
    siteCode,
    eventType,
    onEvent,
  }: EventSourceOpenParametersType): void {
    this.eventSource.addEventListener(eventType, (message) => {
      const event: RealTimeEventType = JSON.parse(message.data);
      const { siteCode: eventSiteCode, ts } = event;

      if (siteCode === eventSiteCode) {
        onEvent(ts);
      }
    });
  }

  public close() {
    this.eventSource.close();
  }
}
