import { UrlEventType, UrlParameter } from '../requester';
import { Nonce } from './nonce';
import {
  IKameleoonData,
  KameleoonData,
  PageViewDataType,
  PageViewParametersType,
} from './types';

/**
 * @class
 * PageView - a class for creating an instance of a page view data
 * */
export class PageView extends Nonce implements IKameleoonData {
  private urlAddress: string;
  private title: string;
  private referrers?: number[];

  /**
   * @param {PageViewParametersType} pageViewParameters - an object with page view parameters
   * */
  constructor({ urlAddress, title, referrers }: PageViewParametersType) {
    super();

    this.urlAddress = urlAddress;
    this.title = title;
    this.referrers = referrers;
  }

  /**
   * @property url - url for the current data type used for events tracking
   * */
  get url(): string {
    return (
      UrlEventType.Page +
      UrlParameter.Href +
      encodeURIComponent(this.urlAddress) +
      UrlParameter.Title +
      encodeURIComponent(this.title) +
      this.getReferresParameter(this.referrers) +
      UrlParameter.Nonce +
      this.nonce
    );
  }

  /**
   * @property data - data of the current class instance
   * */
  get data(): PageViewDataType {
    return {
      urlAddress: this.urlAddress,
      title: this.title,
      referrers: this.referrers,
      type: KameleoonData.PageView,
      nonce: this.nonce,
    };
  }

  private getReferresParameter(
    referrers: PageViewParametersType['referrers'],
  ): string {
    if (referrers) {
      return UrlParameter.ReferrersIndices + JSON.stringify(referrers);
    }

    return '';
  }
}
