import { Ok, Result } from 'ts-res';
import { KameleoonError } from '../kameleoonError/kameleoonError';
import { ClientSettingsType } from '../clientSettings/types';
import { DEFAULT_CAMPAIGN_CONFIGURATION, SESSION_DURATION } from './constants';
import {
  CampaignConfigurationParametersType,
  ConfigurationDataType,
  ConfigurationType,
  ExperimentItemType,
  FeatureFlagType,
  UnsentTargetingDataType,
} from './types';
import { KameleoonDataType } from '../kameleoonData';
import { Requester, GetClientConfigurationResultType } from '../requester';
import { TargetingDataItemType, TargetingDataType } from '../targeting';
import { IStorage, ClientDataType } from '../storage';
import {
  ExternalEventSource,
  IEventSource,
  IExternalEventSourceConstructor,
} from '../eventSource';
import { Milliseconds } from '../types';

interface ICampaignConfiguration {
  initialize: (
    eventSource: IExternalEventSourceConstructor,
  ) => Promise<Result<void, KameleoonError>>;
  addTargetingData: (
    visitorCode: string,
    ...data: KameleoonDataType[]
  ) => Result<void, KameleoonError>;
  getUnsentData: (visitorCode: string) => KameleoonDataType[];
  clearUnsentData: (visitorCode: string) => void;
  onConfigurationUpdate: (callback: () => void) => void;
  readonly experiments: ExperimentItemType[];
  readonly storedTargetingData: TargetingDataType;
  readonly featureFlags: FeatureFlagType[];
  readonly configuration: ConfigurationType;
}

export class CampaignConfiguration implements ICampaignConfiguration {
  private configurationUpdateCallback?: () => void;
  private updateConfigurationIntervalId: NodeJS.Timer | null = null;
  private targetingDataIntervalId: NodeJS.Timer | null = null;
  private unsentTargetingData: UnsentTargetingDataType = {};
  private targetingData: TargetingDataType =
    DEFAULT_CAMPAIGN_CONFIGURATION.targetingData;
  private configurationData: ConfigurationDataType =
    DEFAULT_CAMPAIGN_CONFIGURATION.configurationData;
  private settings: ClientSettingsType;
  private lastUpdate: Date;
  private storage: IStorage<ClientDataType>;
  private requester: Requester;
  private eventSource?: IEventSource;
  private externalClientConfiguration?: GetClientConfigurationResultType;

  constructor({
    settings,
    storage,
    requester,
    externalClientConfiguration,
    targetingCleanupInterval,
  }: CampaignConfigurationParametersType) {
    this.requester = requester;
    this.settings = settings;
    this.lastUpdate = new Date();
    this.storage = storage;
    this.readStorageData();
    this.externalClientConfiguration = externalClientConfiguration;

    if (targetingCleanupInterval) {
      const cleanupIntervalMinutes =
        targetingCleanupInterval < 0 ? 1 : targetingCleanupInterval;

      // --- Note ---
      // We can not make delayed code execution recoverable, as it might lead
      // to targeting data storage size overflow
      try {
        this.targetingDataIntervalId = setInterval(
          () =>
            this.cleanupOutdatedTargetingData.call(this, this.targetingData),
          cleanupIntervalMinutes * Milliseconds.Minute,
        );
      } catch (err) {
        if (this.targetingDataIntervalId) {
          clearInterval(this.targetingDataIntervalId);
        }

        throw err;
      }
    }
  }

  public async initialize(
    externalEventSource: IExternalEventSourceConstructor,
  ): Promise<Result<void, KameleoonError>> {
    const shouldUpdate = this.checkShouldUpdate();

    // --- Initial data fetch ---
    if (shouldUpdate) {
      const updateResult = await this.updateClientConfiguration();

      if (!updateResult.ok) {
        return updateResult;
      }
    }

    // --- Note ---
    // Interval is cleaned up in case real time update
    // was activated while initialized client already exists
    if (
      this.configurationData.configuration.realTimeUpdate &&
      !this.externalClientConfiguration
    ) {
      if (this.updateConfigurationIntervalId) {
        clearInterval(this.updateConfigurationIntervalId);
      }

      this.eventSource = new ExternalEventSource(
        this.settings.siteCode,
        externalEventSource,
      );

      const updateEventCallback = (_timeStamp: number) => {
        this.updateClientConfiguration();

        if (this.configurationUpdateCallback) {
          this.configurationUpdateCallback();
        }
      };

      this.eventSource.open(updateEventCallback);
    } else {
      // --- Note ---
      // Event Source is cleaned up in case real time update
      // was disabled while initialized client already exists
      if (this.eventSource) {
        this.eventSource.close();
      }

      if (this.updateConfigurationIntervalId) {
        clearInterval(this.updateConfigurationIntervalId);
      }

      // --- Note ---
      // Same as targeting data interval we don't want to make failed configuration
      // retrieval recoverable
      try {
        this.updateConfigurationIntervalId = setInterval(
          this.updateClientConfiguration.bind(this),
          this.settings.updateInterval,
        );
      } catch (err) {
        if (this.updateConfigurationIntervalId) {
          clearInterval(this.updateConfigurationIntervalId);
        }

        throw err;
      }
    }

    return Ok();
  }

  public addTargetingData(
    visitorCode: string,
    ...data: KameleoonDataType[]
  ): Result<void, KameleoonError> {
    if (!(visitorCode in this.targetingData)) {
      this.targetingData[visitorCode] = [];
    }

    data.forEach((dataItem) => {
      // --- Note ---
      // Previous check excludes the failure possibility
      (this.targetingData[visitorCode] as TargetingDataItemType[]).push({
        data: dataItem.data,
        lastActivityTime: Date.now(),
      });
    });

    // --- Note ---
    // `targetingData` should not refresh `lastUpdate` time
    const result = this.updateStorageData(this.lastUpdate);

    if (result.ok) {
      data.forEach((dataItem) => {
        if (!this.unsentTargetingData[visitorCode]) {
          this.unsentTargetingData[visitorCode] = [];
        }

        this.unsentTargetingData[visitorCode].push(dataItem);
      });
    }

    return result;
  }

  public getUnsentData(visitorCode: string): KameleoonDataType[] {
    const unsentDataItem = this.unsentTargetingData[visitorCode];

    if (!unsentDataItem) {
      return [];
    }

    return unsentDataItem;
  }

  public clearUnsentData(visitorCode: string): void {
    this.unsentTargetingData[visitorCode] = [];
  }

  public onConfigurationUpdate(callback: () => void): void {
    this.configurationUpdateCallback = callback;
  }

  get storedTargetingData(): TargetingDataType {
    return this.targetingData;
  }

  get experiments(): ExperimentItemType[] {
    return this.configurationData.experiments;
  }

  get featureFlags(): FeatureFlagType[] {
    return this.configurationData.featureFlags;
  }

  get configuration(): ConfigurationType {
    return this.configurationData.configuration;
  }

  private checkShouldUpdate(): boolean {
    const { featureFlags, experiments, configuration } = this.configurationData;
    const { updateInterval } = this.settings;

    if (!featureFlags.length && !experiments.length) {
      return true;
    }

    if (configuration.realTimeUpdate) {
      return true;
    }

    const lastUpdateTime = this.lastUpdate.getTime();
    const updateWindowEdge = Date.now() - updateInterval;

    return lastUpdateTime < updateWindowEdge;
  }

  private cleanupOutdatedTargetingData(data: TargetingDataType): void {
    const currentTime = Date.now();
    const resultTargetingData: TargetingDataType = { ...data };

    for (const [key, value] of Object.entries(resultTargetingData)) {
      if (value) {
        const resultValue = value.filter((item) => {
          const isDataExpired =
            item.lastActivityTime + SESSION_DURATION < currentTime;

          return isDataExpired;
        });

        resultTargetingData[key] = resultValue;
      } else {
        resultTargetingData[key] = [];
      }
    }

    this.targetingData = resultTargetingData;
    // --- Note ---
    // `targetingData` should not refresh `lastUpdate` time
    this.updateStorageData(this.lastUpdate).throw();
  }

  private readStorageData(): void {
    const result = this.storage.read();

    if (result.ok) {
      const { kameleoonTargetingData, kameleoonConfiguration, lastUpdate } =
        result.data;

      this.configurationData = kameleoonConfiguration;
      this.targetingData = kameleoonTargetingData;
      this.lastUpdate = new Date(Date.parse(lastUpdate));
    }
  }

  private async updateClientConfiguration(
    timeStamp?: number,
  ): Promise<Result<boolean, KameleoonError>> {
    // --- Note ---
    // if `externalClientConfiguration` is passed, fetching is disabled and passed config is used instead
    if (this.externalClientConfiguration) {
      // --- Note ---
      // `featureFlags` field is omitted (v1)
      // instead `featureFlagConfigurations` is used (v2)
      const { configuration, experiments, featureFlagConfigurations } =
        this.externalClientConfiguration;

      this.configurationData = {
        configuration,
        experiments,
        featureFlags: featureFlagConfigurations,
      };
    } else {
      const clientConfigurationResult =
        await this.requester.getClientConfiguration(timeStamp);

      if (!clientConfigurationResult.ok) {
        return clientConfigurationResult;
      }

      // --- Note ---
      // `featureFlags` field is omitted (v1)
      // instead `featureFlagConfigurations` is used (v2)
      const { configuration, experiments, featureFlagConfigurations } =
        clientConfigurationResult.data;

      this.configurationData = {
        configuration,
        experiments,
        featureFlags: featureFlagConfigurations,
      };
    }

    const result = this.updateStorageData(new Date());

    if (!result.ok) {
      return result;
    }

    return Ok(true);
  }

  private updateStorageData(lastUpdate: Date): Result<void, KameleoonError> {
    const data: ClientDataType = {
      kameleoonTargetingData: this.targetingData,
      kameleoonConfiguration: this.configurationData,
      lastUpdate: lastUpdate.toString(),
    };

    return this.storage.write(data);
  }
}
