import { Err, Ok, Result } from 'ts-res';
import { Cookie, KAMELEOON_COOKIE_KEY } from './constants';
import {
  GetResultCookieParametersType,
  ClientGetVisitorCodeParametersType,
  ServerGetVisitorCodeParametersType,
  MobileGetVisitorCodeParametersType,
} from './types';

/**
 * @abstract
 * KameleoonUtils - an abstract class for extending kameleoon utils with pre-defined method signatures
 * */
export abstract class KameleoonUtils {
  abstract getVisitorCode(
    param:
      | ClientGetVisitorCodeParametersType
      | ServerGetVisitorCodeParametersType
      | MobileGetVisitorCodeParametersType,
  ): string;

  protected generateRandomString(length: number): string {
    const characters = 'abcdefghijklmnopqrstuvwxyz0123456789';
    let randomString = '';

    if (length < 0) {
      return randomString;
    }

    for (let i = 0; i < length; i++) {
      randomString += characters[Math.floor(Math.random() * characters.length)];
    }

    return randomString;
  }

  protected getCookieVisitorCode(cookie: string): Result<string, void> {
    const pairs = cookie.split(Cookie.PairsDelimiter);
    const pair = pairs?.find((pair) => {
      const [key, _] = pair.split(Cookie.KeyValueDelimiter) as [string, string];

      return key === KAMELEOON_COOKIE_KEY;
    });

    if (pair) {
      const [_, value] = pair.split(Cookie.KeyValueDelimiter);

      return Ok(value);
    }

    return Err();
  }

  protected getResultCookie({
    domain,
    visitorCode,
  }: GetResultCookieParametersType): string {
    const yearSeconds = 60 * 60 * 24 * 365;
    const cookieArray = [
      KAMELEOON_COOKIE_KEY + Cookie.KeyValueDelimiter + visitorCode,
      Cookie.MaxAge + Cookie.KeyValueDelimiter + yearSeconds,
      Cookie.Path + Cookie.KeyValueDelimiter + '/',
      Cookie.Domain + Cookie.KeyValueDelimiter + domain,
    ];

    return cookieArray.join(Cookie.PairsDelimiter);
  }
}
