import {
  Ok,
  Err,
  Result,
  KameleoonError,
  IExternalStorage,
  KameleoonException,
  KameleoonStorageKey,
  IExternalStorageConstructor,
} from '@kameleoon/javascript-sdk-core';

export class StorageConstructor implements IExternalStorageConstructor {
  public initialize(key: KameleoonStorageKey): IExternalStorage {
    const storage = new Storage(key);

    return storage;
  }
}

class Storage implements IExternalStorage {
  private storageKey: KameleoonStorageKey;

  constructor(key: KameleoonStorageKey) {
    this.storageKey = key;
  }

  get key(): KameleoonStorageKey {
    return this.storageKey;
  }

  public read(): Result<string, KameleoonError> {
    const data = localStorage.getItem(this.storageKey);

    if (!data) {
      return Err(
        new KameleoonError(KameleoonException.StorageRead, this.storageKey),
      );
    }

    return Ok(data);
  }

  public write(data: string): Result<void, KameleoonError> {
    try {
      localStorage.setItem(this.storageKey, data);

      return Ok();
    } catch (err) {
      return Err(new KameleoonError(KameleoonException.StorageWrite, err));
    }
  }
}
