import { Nonce } from './nonce';
import { UserAgentDataType, IKameleoonData, KameleoonData } from './types';

/**
 * @class
 * UserAgent - a class for creating an instance for user agent
 * */
export class UserAgent extends Nonce implements IKameleoonData {
  private value: string;

  constructor(value: string) {
    super();

    this.value = value;
  }

  /**
   * @property url - url for the current data type used for events tracking
   * */
  get url(): string {
    return '';
  }

  /**
   * @property data - data of the current class instance
   * */
  get data(): UserAgentDataType {
    return {
      value: this.value,
      type: KameleoonData.UserAgent,
      nonce: this.nonce,
    };
  }
}
