import { Ok, Result } from 'ts-res';
import { KameleoonError } from '../../kameleoonError';
import { ExperimentVariationsType } from '../../storage';
import { EvaluationDataType, MatchTypeVariation } from '../types';
import { ConditionDataType, ICondition } from './types';

export class TargetExperimentCondition implements ICondition {
  private inverseResult: boolean;
  private experimentId?: number | null;
  private variationId?: number | null;
  private matchType?: MatchTypeVariation | null;

  constructor({
    variation,
    experiment,
    isInclude,
    variationMatchType,
  }: ConditionDataType) {
    this.inverseResult = !(isInclude ?? true);
    this.experimentId = experiment;
    this.variationId = variation;
    this.matchType = variationMatchType;
  }

  evaluate({
    variationData,
  }: EvaluationDataType): Result<boolean, KameleoonError> {
    if (!this.experimentId) {
      return Ok(false);
    }

    if (this.matchType === MatchTypeVariation.EXACT && !this.variationId) {
      Ok(false);
    }

    const result = this.checkCondition({
      variationId: this.variationId,
      experimentId: this.experimentId,
      variationData,
    });

    return Ok(this.inverseResult ? !result : result);
  }

  private checkCondition({
    variationId,
    experimentId,
    variationData,
  }: {
    variationId?: number | null;
    experimentId: number;
    variationData: ExperimentVariationsType;
  }): boolean {
    const experimentIdString = String(experimentId);
    const variation = variationData[experimentIdString];

    switch (this.matchType) {
      case MatchTypeVariation.EXACT: {
        return variation?.variationId === variationId;
      }
      case MatchTypeVariation.ANY:
        return Boolean(variation);
      default:
        return false;
    }
  }
}
