import { Err, Ok, Result } from 'ts-res';
import {
  KameleoonData,
  CustomDataType,
  KameleoonDataItemType,
} from '../../kameleoonData';
import { KameleoonError, KameleoonException } from '../../kameleoonError';
import { EvaluationDataType, MatchTypeCustomData } from '../types';
import { ConditionDataType, ICondition } from './types';

export class CustomDataCondition implements ICondition {
  private customDataIndex: number;
  private matchType: MatchTypeCustomData | null;
  private inverseResult: boolean;
  private conditionValue: string | null;
  private error?: KameleoonError;

  constructor({
    customDataIndex,
    valueMatchType,
    isInclude,
    value,
  }: ConditionDataType) {
    this.customDataIndex = Number(customDataIndex);
    this.inverseResult = !(isInclude ?? true);
    this.matchType = valueMatchType;
    this.conditionValue = value;
  }

  public evaluate({
    targetingData,
  }: EvaluationDataType): Result<boolean, KameleoonError> {
    const isCustomData = (
      data: KameleoonDataItemType,
    ): data is CustomDataType => data.type === KameleoonData.CustomData;

    const filteredData = targetingData
      .map((item) => item.data)
      .filter(isCustomData)
      .filter((item) => item.index === this.customDataIndex);

    if (filteredData.length) {
      const lastDataItem = filteredData[filteredData.length - 1];
      const { value: customDataValue } = lastDataItem;
      const result = this.inverseResult
        ? !this.checkCondition(customDataValue)
        : this.checkCondition(customDataValue);

      if (this.error) {
        return Err(this.error);
      }

      return Ok(result);
    }

    return Ok(this.matchType === MatchTypeCustomData.UNDEFINED);
  }

  private checkCondition(customDataValue: CustomDataType['value']): boolean {
    if (this.conditionValue === null) {
      return false;
    }

    // --- Note ---
    // `AMONG_VALUES` is checked separately to avoid recursion overhead
    // for `JSON.parse` inside `this.parseArray`
    if (this.matchType === MatchTypeCustomData.AMONG_VALUES) {
      const result = this.parseArray(this.conditionValue);

      if (!result.ok) {
        this.error = result.error;

        return false;
      }

      const resultData = new Set(result.data);

      return Array.isArray(customDataValue)
        ? customDataValue.some((value) => resultData.has(value))
        : result.data.includes(customDataValue);
    }

    if (Array.isArray(customDataValue)) {
      return customDataValue.some((value) => this.checkCondition(value));
    }

    switch (this.matchType) {
      case MatchTypeCustomData.CONTAINS:
        return customDataValue.includes(this.conditionValue);
      case MatchTypeCustomData.EXACT:
        return customDataValue === this.conditionValue;
      case MatchTypeCustomData.REGULAR_EXPRESSION:
        return this.parseRegExp(this.conditionValue).test(customDataValue);
      case MatchTypeCustomData.LOWER:
        return Number(customDataValue) < Number(this.conditionValue);
      case MatchTypeCustomData.EQUAL:
        return Number(customDataValue) === Number(this.conditionValue);
      case MatchTypeCustomData.GREATER:
        return Number(customDataValue) > Number(this.conditionValue);
      case MatchTypeCustomData.TRUE:
        return customDataValue.toLowerCase() === 'true';
      case MatchTypeCustomData.FALSE:
        return customDataValue.toLowerCase() === 'false';
      default:
        return false;
    }
  }

  private parseRegExp(value: string): RegExp {
    if (value[0] === '/') {
      const [_, expression, flags] = value.split('/');

      return new RegExp(expression, flags);
    }

    return new RegExp(value);
  }

  private parseArray(value: string): Result<string[], KameleoonError> {
    try {
      const result = JSON.parse(value);

      if (Array.isArray(result)) {
        return Ok(result);
      }
    } catch (err) {
      return Err(
        new KameleoonError(KameleoonException.AmongValuesCheck, err, value),
      );
    }

    return Ok([]);
  }
}
